#logo {
  width: 225px;
  margin-bottom: 100px; }

h1 {
  margin-bottom: 50px;
  font-family: "museo-slab", serif;
  font-size: 14em; }

p {
  line-height: 1.3;
  font-family: "essonnes-text", serif;
  font-size: 2.5em; }
  p.contact-info {
    margin-top: 2em;
    font-size: 1.2em; }
    p.contact-info a {
      position: relative;
      color: white;
      text-decoration: none; }
      p.contact-info a#irrealis {
        font-style: italic; }
        p.contact-info a#irrealis:before {
          content: '';
          background: white;
          position: absolute;
          bottom: -4px;
          left: 50%;
          width: 50%;
          height: 1px;
          transform: translateX(-50%);
          transition: width .2s ease, bottom .2s ease; }
        p.contact-info a#irrealis:hover:before {
          bottom: -2px;
          width: 100%; }
